import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Product } from '../entity/product';
import {HttpHeaders } from '@angular/common/http';
import { BehaviorSubject } from 'rxjs';
import { Category } from '../entity/category';
@Injectable({
  providedIn: 'root'
})
export class CategoryService {
 
  private REST_API_SERVER = "http://localhost:3000";
  auth_token:any;
  constructor(private httpClient: HttpClient) { 
    this.auth_token=localStorage.getItem('token');
   
   
  }

  public add(post:any){
    const headers1 = new HttpHeaders({
      'Content-Type': 'application/json',
      'Authorization': `Bearer ${this.auth_token}`
    })
    return this.httpClient.post(this.REST_API_SERVER+'/category/',post,{headers:headers1});
  }
  public list(){
    const headers1 = new HttpHeaders({
      'Content-Type': 'application/json',
      'Authorization': `Bearer ${this.auth_token}`
    })
    return this.httpClient.get<Category[]>(this.REST_API_SERVER+'/category/',{headers:headers1});
  }
  public number(){
    return this.httpClient.get(this.REST_API_SERVER+'/category/count');
  }
  
  
}
