import { ApplicationRef, ChangeDetectorRef, Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import { BehaviorSubject, merge, of, Subscription } from 'rxjs';
import { ConfirmationDialogComponent } from 'src/app/confirmation-dialog/confirmation-dialog.component';
import { Product } from 'src/app/entity/product';
import { ProductService } from 'src/app/service/product.service';
class F{
  data:Product[];
    count:number;
}
@Component({
  selector: 'app-list-product',
  templateUrl: './list-product.component.html',
  styleUrls: ['./list-product.component.css']
})
export class ListProductComponent implements OnInit {
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  dataSource:MatTableDataSource<Product>;
  products$: BehaviorSubject<F>;
  loading:boolean=true;
  msg$: BehaviorSubject<String>;
  serviceSubscribe:Subscription;
  length=0;pageSize=10;
  displayedColumns  :  string[] = ['id','name', 'price', 'categoryId','actions'];
  constructor(private productService:ProductService,private router: Router,public dialog: MatDialog) {
    this.dataSource=new MatTableDataSource<Product>();
    this.loading=true;
    this.products$ = new BehaviorSubject<F>({count:0,data:[]});
    this.msg$ = new BehaviorSubject<String>("");
   }
  
  ngOnInit(){
   
  this.productService.listLimit('id','asc',0,this.pageSize).subscribe((result:F)=>{
    this.loading=false;
    this.products$.next(result);
  });
  this.serviceSubscribe = this.products$.subscribe((res:F) => {
    
      this.dataSource.data = res.data;
      this.length=res.count;
     
     
  })
}
getServerData(e){
 let active=this.sort.active;
 let dir=this.sort.direction;
  this.pageSize=e.pageSize;
  this.productService.listLimit(active,dir,e.pageIndex*e.pageSize,e.pageSize).subscribe((result:F)=>{
    this.loading=false;
    this.products$.next(result);
   
  });
}
applySort(e){
   const index=this.paginator.pageIndex;
   const size=this.paginator.pageSize;
  
  this.productService.listLimit(e.active,e.direction,index*size,size).subscribe((result:F)=>{
    this.loading=false;
    this.products$.next(result);
   
  });
}

ngAfterViewInit(): void {
  this.sort.sortChange.subscribe((res)=>{

    console.log(res);
  });
 //this.dataSource.sort=this.sort;
  
 
     
}
applyFilter(event: Event) {
  const filterValue = (event.target as HTMLInputElement).value;
  this.dataSource.filter = filterValue.trim().toLowerCase();
}

goToProduct()
{
  this.router.navigate(['products/add']);

}
edit(data: Product) {
  const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
            data: data
  });

  dialogRef.afterClosed().subscribe(result => {
    if (result) {
     
     this.productService.edit(result).subscribe((res)=>{
      this.ngOnInit();
     });
    }
  });
}

delete(id: any) {
  const dialogRef = this.dialog.open(ConfirmationDialogComponent);

  dialogRef.afterClosed().subscribe(result => {
    if (result) {
     // this.productService.remove(id);
    }
  });
}
trackTask(t:any)
{
 
  return t.id;
}
}
