import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';

import { AppComponent } from './app.component';


import { Routes, RouterModule } from '@angular/router'; // CLI imports router
import { HttpClientModule } from '@angular/common/http';

import { BrowserAnimationsModule } from '@angular/platform-browser/animations'; 
import { SharedModule } from './shared/shared.module';
import {DashboardComponent} from './admin/pages/dashboard/dashboard.component';
import { AddProductComponent } from './admin/pages/add-product/add-product.component';
import { LoginComponent } from './home/pages';
import { ProductComponent } from './admin/pages/product/product.component';
import { EditProductComponent } from './admin/pages/edit-product/edit-product.component';
import { UserComponent } from './admin/pages/user/user.component';
import { ListProductComponent } from './admin/pages/list-product/list-product.component';
import { ConfirmationDialogComponent } from './confirmation-dialog/confirmation-dialog.component';

const routes: Routes = [
  { path:'',component:LoginComponent},
  { path:'products',component:ProductComponent,
   children:[
    { path:'',component:ListProductComponent},
     { path:'add',component:AddProductComponent},
      { path:'edit',component:EditProductComponent},
    ]
  },
  { path:'users',component:UserComponent},
  { path:'dashboard',component:DashboardComponent}
  
]; 

@NgModule({
  declarations: [
    AppComponent,
    AddProductComponent,ProductComponent,LoginComponent, ListProductComponent,EditProductComponent, ConfirmationDialogComponent
    
  ],
  imports: [BrowserAnimationsModule,
HttpClientModule,
BrowserModule,
    SharedModule,
    RouterModule.forRoot(routes)
   ],
  providers: [],
  bootstrap: [AppComponent],entryComponents: [ConfirmationDialogComponent]
})
export class AppModule { }
