import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Product } from '../entity/product';
import {HttpHeaders } from '@angular/common/http';
import { BehaviorSubject } from 'rxjs';
import { delay } from 'rxjs/operators';
class F{
  data:Product[];
    count:number;
}
@Injectable({
  providedIn: 'root'
})
export class ProductService {
  persons$: BehaviorSubject<Product[]>;
  persons: Array<Product> = [];
  private REST_API_SERVER = "http://localhost:3000/api";
  auth_token:any;
  constructor(private httpClient: HttpClient) { 
    this.auth_token=localStorage.getItem('token');
    this.persons$ = new BehaviorSubject([]);
   
  }

  public add(post:any){
    const headers1 = new HttpHeaders({
      'Content-Type': 'application/json',
      'Authorization': `Bearer ${this.auth_token}`
    })
    return this.httpClient.post(this.REST_API_SERVER+'/products/',post,{headers:headers1});
  }
  public edit(post:Product){
    const headers1 = new HttpHeaders({
      'Content-Type': 'application/json',
      'Authorization': `Bearer ${this.auth_token}`
    })
    return this.httpClient.put(this.REST_API_SERVER+'/products/',post,{headers:headers1});
  }
  public list1(){
    const headers1 = new HttpHeaders({
      'Content-Type': 'application/json',
      'Authorization': `Bearer ${this.auth_token}`
    })
    return this.httpClient.get<F>(this.REST_API_SERVER+'/products/',{headers:headers1});
  }
  
  public listLimit(sort_by,sort_dir,offset,limit){
    const headers1 = new HttpHeaders({
      'Content-Type': 'application/json',
      'Authorization': `Bearer ${this.auth_token}`
    })
    return this.httpClient.get<F>(`${this.REST_API_SERVER}/products/query?sort_by=${sort_by}&sort_dir=${sort_dir}&offset=${offset}&limit=${limit}`,{headers:headers1});
  }
  
  public number(){
    return this.httpClient.get(this.REST_API_SERVER+'/products/count');
  }
  
  
}
