import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { Category } from 'src/app/entity/category';
import { ProductService } from 'src/app/service/product.service';
import { Product } from '../entity/product';
import { CategoryService } from '../service/category.service';

@Component({
  selector: 'app-confirmation-dialog',
  templateUrl: './confirmation-dialog.component.html',
  styleUrls: ['./confirmation-dialog.component.css']
})
export class ConfirmationDialogComponent implements OnInit {
category:Category[];
  formGroup: FormGroup;
  post:any;
  submit_err:any={
    status:false,message:''
  };
  constructor(private formBuilder: FormBuilder,private productService:ProductService,
    private router: Router,public dialogRef: MatDialogRef<ConfirmationDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: Product,private categoryService:CategoryService) { }

  ngOnInit(): void {
    this.categoryService.list().subscribe(res=>{
      this.category=res;
    });
    this.createForm();
  }
  sel(id)
  {
  
    return (this.data.category.id==id);
  }
  createForm() {
    let emailregex: RegExp = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    this.formGroup = this.formBuilder.group({
      'name': [this.data.name, Validators.required],
      'price': [this.data.price, Validators.required],
      'category': [this.data.category.id, Validators.required]
     
    });
     
  }
  save(): void {
    this.dialogRef.close(Object.assign(new Product(),{id:this.data.id,...this.formGroup.value}));
  }
  
  onSubmit(val)
  {
   
    this.post=val;
    console.log(val);
    this.productService.add(this.post).subscribe((data:any)=>{
       console.log(data);
       this.router.navigate(['products']);
    },
    (error)=>{
      console.error(error);
    });
  }

}
